import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import qs from 'querystring'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userLoggedIn: JSON.parse(localStorage.getItem("userLoggedIn")),
    categories:JSON.parse(localStorage.getItem("categories")),
    detail:JSON.parse(localStorage.getItem("detail")),
    loading:false
  },
  mutations:{
    setLoading(state,load){
      state.loading = load
    }
  },
  actions:{
    getCategories({state},auth_key){
      axios
          .get(process.env.VUE_APP_URL+'categories',{
              headers: {
                  'Authorization':'Bearer '+auth_key
              }
          })
          .then(res => {
              localStorage.setItem('categories',JSON.stringify(res.data))
              state.loading = false
              location.reload()
          })
    },
    login : ({state,dispatch},user) => {
      axios
          .post(process.env.VUE_APP_URL+'login',qs.stringify(user),{
              headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
              }
          })
          .then((res) => {
              state.loading = false
              localStorage.setItem('userLoggedIn',JSON.stringify(res.data))
              dispatch('getDetail',res.data.auth_key)
          })
    },
    logout : ({state}) => {
      axios
      .get(process.env.VUE_APP_URL+'logout',{
        headers:{
            'Authorization':'Bearer '+state.userLoggedIn.auth_key
        }
      })
      .then(() => {
        localStorage.clear()
        location.reload()
      })
        
    },
    getDetail:({dispatch},auth_key)=>{
        axios
        .get(process.env.VUE_APP_URL+'detail',{
            headers:{
                'Authorization':'Bearer '+auth_key
            } 
        })
        .then(res=>{    
            localStorage.setItem('detail',JSON.stringify(res.data))
            if(res.data.exam != null){
              dispatch('getCategories',auth_key)
            }
        })
    },
    setAnswer:({state},data) => {
      axios
          .post(process.env.VUE_APP_URL+'answer',qs.stringify(data),{
              headers: {
                  'Content-Type': 'application/x-www-form-urlencoded',
                  'Authorization':'Bearer '+state.userLoggedIn.auth_key
              }
          })
    }
  },
})
